<template>
  <div class="order-detail">
        <!-- 面包屑 start -->
        <el-breadcrumb separator=">" class="breadcrumb">
            <el-breadcrumb-item>统计报表</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/statistical/orderform/allform' }">订单报表</el-breadcrumb-item>
            <el-breadcrumb-item>订单详情</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 面包屑 end -->

        <div class="content-box">
          <h3 class="title">订单详情</h3>

          <div class="detail-content">

            <!-- 订单编号 -->
            <div class="item order-id">
              <div class="item-left">
                <span>订单号</span>
              </div>
              <div class="item-right">
                <span>{{ orderid }}</span>
              </div>
            </div>

            <!-- 订单来源 -->
            <div class="item order-source clearfix">
              <div class="left">
                <div class="item-left">
                  <span>订单来源</span>
                </div>
                <div class="item-right">
                  <span>{{ OrderSource.toString(orderSource) }}</span>
                </div>
              </div>

              <div class="right">
                <div class="item-left">
                  <span>点单人</span>
                </div>
                <div class="item-right">
                  <span>{{ customerName }}</span>
                </div>
              </div>
            </div>

            <!-- 基本信息 -->
            <order-base :baseinfo="baseinfo"></order-base>

            <!-- 支付信息 -->
            <order-nopay v-if="!isPay" :payinfo="payinfo"></order-nopay>
            <order-pay  v-if="isPay" :payinfo="payinfo"></order-pay>

            <!-- 商品信息 -->
            <order-shopinfo :shopinfo="shopinfo"></order-shopinfo>

            <!-- 关闭订单信息 -->
            <order-closeinfo v-if="orderStatus === OrderListStatue.CLOSE" :statusinfo="closeinfo"></order-closeinfo>
          
            <!-- 反结账订单信息 -->
            <order-closeinfo v-if="orderStatus === OrderListStatue.BACK" :statusinfo="backinfo"></order-closeinfo>

            <!-- 退款成功 -->
            <success-refund v-if="orderStatus === OrderListStatue.REFUNDSUCCESS" :statusinfo="successRefundinfo"></success-refund>

            <!-- 退款失败 -->
            <fail-refund v-if="orderStatus === OrderListStatue.REFUNDFAIL" :statusinfo="failRefundinfo"></fail-refund>
          </div>
        </div>
  </div>
</template>

<script>
import moment from "moment";
import OrderBase from "../../components/orderBase";
import OrderNopay from "../../components/orderNopay";
import OrderPay from "../../components/orderPay";
import OrderShopinfo from "../../components/orderShopinfo";
import OrderCloseinfo from "../../components/orderCloseinfo";
import SuccessRefund from "../../components/ordersuccessRefund";
import FailRefund from "../../components/orderfailRefund";

import { OrderSource, OrderListStatue, payStatus } from "@/config/cfg";
import { orderList } from "../../api";

export default {
  components: {
    OrderBase,
    OrderNopay,
    OrderPay,
    OrderShopinfo,
    OrderCloseinfo,
    SuccessRefund,
    FailRefund
  },
  data() {
    return {
      orderinfo: null,
      orderid: "",
      orderSource: 1,
      customerName: "",
      orderStatus: 1,

      isPay: false,

      //基本信息
      baseinfo: {
        order_time: "",
        order_water_num: "",
        seat_name: "",
        customer_num: 13,
        seat_region: "", //餐桌区域
        dine_way: 1,
        seat_type: "", //餐桌类型
        order_status: 1,
        is_invoicing: 1
      },

      // 支付信息
      payinfo: {
        employee_name: "",
        pay_time: "",
        pay_status: 1,
        pay_way: 0,
        order_fee: 0,
        order_waiver_fee: 0,
        order_payable: 0,
        paid_price: 0
      },

      // 商品信息
      shopinfo: {
        list: [
          {
            id: "",
            food_id: "",
            food_name: "",
            food_price: 0,
            food_num: 0,
            food_category: null,
            food_price_sum: 0,
            food_attach_list: null,
            food_unit: null,
            unit_num: 0
          }
        ],
        listNum: 0,
        listTotalPrice: 0,
        remark: ""
      },

      //关闭信息
      closeinfo: {
        title: "关闭订单信息",
        label: "关闭",
        name: "",
        reason: "",
        time: ""
      },

      //反结信息
      backinfo: {
        title: "反结账信息",
        label: "反结账",
        name: "",
        reason: "",
        time: ""
      },

      //退款成功
      successRefundinfo: {
        name: "",
        reason: "",
        reason_sq: "",
        time: "",
        time_sq: "",
        price: 0
      },

      //退款失败
      failRefundinfo: {
        name: "",
        reason: "",
        reason_sq: "",
        time: "",
        time_sq: "",
        price: 0
      },

      OrderSource: OrderSource,
      OrderListStatue:OrderListStatue
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      this.orderid = this.$route.query.orderid;

      let data = {
        get_order_list: 1,
        order_id: this.orderid
      };

      orderList(data, resp => {
        if (resp.ret === 0) {
          this.orderinfo = (resp.data.order_list || [])[0] || {};

          this.orderSource = this.orderinfo.order_from;
          this.customerName = this.orderinfo.customer_name;
          this.orderStatus = this.orderinfo.order_status;

          //订单基本信息
          if (this.orderinfo.order_time) {
            var orderTime = moment(this.orderinfo.order_time * 1000).format(
              "YYYY-MM-DD HH:mm:ss"
            );
          }
          this.baseinfo.order_time = orderTime;
          this.baseinfo.order_water_num = this.orderinfo.order_water_num;
          this.baseinfo.seat_name = (this.orderinfo.seat || {}).seat_name;
          this.baseinfo.seat_region = (this.orderinfo.seat || {}).seat_region;
          this.baseinfo.seat_type = (this.orderinfo.seat || {}).seat_type;
          this.baseinfo.customer_num = this.orderinfo.customer_num;
          this.baseinfo.dine_way = this.orderinfo.dine_way;
          this.baseinfo.order_status = this.orderinfo.order_status;
          this.baseinfo.is_invoicing = this.orderinfo.is_invoicing;

          //支付信息
          if (
            this.orderStatus === OrderListStatue.PAY ||
            this.orderStatus === OrderListStatue.BACK ||
            this.orderStatus === OrderListStatue.REFUNDSUCCESS ||
            this.orderStatus === OrderListStatue.REFUNDFAIL ||
            this.orderStatus === OrderListStatue.IOU
          ) {
            this.isPay = true;
          }

          // 是否已经支付
          let ispayStatus;
          if (
            this.orderStatus === OrderListStatue.PAY ||
            this.orderStatus === OrderListStatue.BACK ||
            this.orderStatus === OrderListStatue.REFUNDSUCCESS ||
            this.orderStatus === OrderListStatue.REFUNDFAIL
          ) {
            ispayStatus = payStatus.YES;
          } else {
            ispayStatus = payStatus.NO;
          }

          //支付时间
          if (this.orderinfo.pay_time) {
            var paytime = moment(this.orderinfo.pay_time * 1000).format(
              "YYYY-MM-DD HH:mm:ss"
            );
          }

          //支付信息
          this.payinfo.employee_name = this.orderinfo.employee_name;
          this.payinfo.pay_time = paytime;
          this.payinfo.pay_status = ispayStatus;
          this.payinfo.pay_way = this.orderinfo.pay_way;
          this.payinfo.order_fee = this.orderinfo.order_fee;
          this.payinfo.order_waiver_fee = this.orderinfo.order_waiver_fee || 0;
          this.payinfo.order_payable = this.orderinfo.order_payable;
          this.payinfo.paid_price = this.orderinfo.paid_price;

          //商品信息
          this.shopinfo.list = this.orderinfo.food_list || [];
          this.shopinfo.remark = this.orderinfo.order_remark;
          this.shopinfo.list.forEach(item => {
            this.shopinfo.listNum += item.food_num || 0;
            this.shopinfo.listTotalPrice += item.food_price_sum || 0;
          });

          //订单关闭信息
          if (this.orderinfo.close_time) {
            var closetime = moment(this.orderinfo.close_time * 1000).format(
              "YYYY-MM-DD HH:mm:ss"
            );
          }
          this.closeinfo.time = closetime;
          this.closeinfo.name = (this.orderinfo.status_info || {}
          ).employee_name;
          this.closeinfo.reason = (this.orderinfo.status_info || {}
          ).made_sf_reson;

          //反结账信息
          if (this.orderinfo.checkout_time) {
            var checkouttime = moment(
              this.orderinfo.checkout_time * 1000
            ).format("YYYY-MM-DD HH:mm:ss");
          }
          this.backinfo.time = checkouttime;
          this.backinfo.name = (this.orderinfo.status_info || {}).employee_name;
          this.backinfo.reason = (this.orderinfo.status_info || {}
          ).made_sf_reson;

          //退款成功信息
          if (this.orderinfo.refunds_time) {
            var refundstime = moment(this.orderinfo.refunds_time * 1000).format(
              "YYYY-MM-DD HH:mm:ss"
            );
          }
          if ((this.orderinfo.status_info || {}).made_time) {
            var refundstimesq = moment(
              (this.orderinfo.status_info || {}).made_time * 1000
            ).format("YYYY-MM-DD HH:mm:ss");
          }
          this.successRefundinfo.time = refundstime;
          this.successRefundinfo.time_sq = refundstimesq;
          this.successRefundinfo.name = (this.orderinfo.status_info || {}
          ).employee_name;
          this.successRefundinfo.reason = (this.orderinfo.status_info || {}
          ).made_reson;
          this.successRefundinfo.reason_sq = (this.orderinfo.status_info || {}
          ).made_sf_reson;

          //退款失败原因
          if (this.orderinfo.refunds_fail_time) {
            var refundftime = moment(this.orderinfo.refunds_fail_time * 1000).format(
              "YYYY-MM-DD HH:mm:ss"
            );
          }
          if ((this.orderinfo.status_info || {}).made_time) {
            var refundftimesq = moment(
              (this.orderinfo.status_info || {}).made_time * 1000
            ).format("YYYY-MM-DD HH:mm:ss");
          }
          this.failRefundinfo.time = refundftime;
          this.failRefundinfo.time_sq = refundftimesq;
          this.failRefundinfo.name = (this.orderinfo.status_info || {}
          ).employee_name;
          this.failRefundinfo.reason = (this.orderinfo.status_info || {}
          ).made_reson;
          this.failRefundinfo.reason_sq = (this.orderinfo.status_info || {}
          ).made_sf_reson;

          console.log(this.orderinfo);
        } else {
          console.warn("获取订单详情错误！");
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.content-box {
  background: #ffffff;
  box-shadow: 0 2px 4px 0 #becaeb;
}
.title {
  @include fc(14px, #666666);
  background-color: #f6f8fc;
  height: 40px;
  line-height: 40px;
  padding-left: 14px;
}
.breadcrumb {
  margin-bottom: 14px;
}
.detail-content {
  padding: 40px 114px 40px 114px;
}
.item {
  font-size: 0;

  .item-left {
    @include fc(14px,#000);
    display: inline-block;
    width: 106px;
    opacity: 0.5708;
    text-align: right;
  }
  .item-right {
    @include fc(14px,#333);
    display: inline-block;
    padding-left: 14px;
  }
}

.order-id {
  margin-bottom: 20px;
}
.order-source {
  margin-bottom: 20px;
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";

.order-detail {
  .el-breadcrumb {
    @include fc(12px, #333333);
  }

  .el-breadcrumb__item:last-child .el-breadcrumb__item__inner {
    color: #4877e7;
  }

  .el-breadcrumb__item__inner:hover {
    color: #4877e7;
  }
}
</style>





